<template>
  <transition name='fade' mode='out-in'>
    <div>
      <h1>Welcome to another BORING  practice app with Vue!</h1>
      <a class='platform' href="https://www.etoro.com.cn/discover/markets" rel="noopener">test</a>
      <router-link to="/About">Go to About to See Page Level Code Splitting</router-link>
      <h1>afwafwafwafwaf</h1>
      <search-bar></search-bar>
      <below-fold></below-fold>
    </div>
  </transition>
</template>

<script>
  import SearchBar from '../../components/SearchBar.vue'

  const BelowFold = () => import(
    /* webpackChunkName: "below-fold" */ './SearchBelowFold.vue'
  )

  export default {
    name: 'Search',
    components: {
      SearchBar,
      BelowFold
    },
    data () {
      return {
        msg: 'Welcome to Your Vue.js App'
      }
    }
  }
</script>

<style>
  * {
    box-sizing:border-box;
  }
  .platform {
    display:block;
  }
  .fade-enter-active, .fade-leave-active {
    transition: opacity 0.3s
  }

  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0
  }
</style>


